import { lazy } from 'react';
import { IRoute } from 'interfaces';
import Loading from 'components/Loading';

export const HomeRoutes: IRoute[] = [
  {
    path: '/',
    exact: true,
    redirect: '/home',
    fallback: <Loading />
  },
  {
    path: '/home',
    component: lazy(() => import('./HomePage')),
    exact: false,
    fallback: <Loading />
  },
];
