
import styled from "styled-components";
import Router from "./components/Router";
import { HomeRoutes } from "./modules/Home/routes";

const Container = styled.main`
  max-width: 860px;
  padding: 0 10px;
  height: auto;
  margin: auto;
`;

function App() {
  const routes = [...HomeRoutes];

  return (
    <Container>
      <Router routes={routes} />
    </Container>
  );
}

export default App;
